
a,
a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.container {
  margin: 20px 50px;
}
#navbar {
  background: #ccc;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #fff;
}
#navbar .line {
  margin: 0 10px;
  font-size: 30px;
}
#navbar .nav-logo {
  font-size: 30px;
  margin: 0 100px;
}
#navbar .user-menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0 30px;
}
#navbar .user-menu .user {
  height: 40px;
  margin: 0 5px;
}
#navbar .user-menu .user input {
  display: none;
}
#navbar .user-menu .user label {
  cursor: pointer;
}
#navbar .user-menu .user .drop-menu {
  position: absolute;
  background: #ccc;
  display: none;
}
#navbar .user-menu .user .drop-menu .drop-item {
  padding: 3px 10px;
  cursor: pointer;
  border-bottom: 1px solid #fff;
}
#navbar .user-menu .user .drop-menu .drop-item:hover {
  background: #aaa;
}
#navbar .user-menu .user input:checked ~ .drop-menu {
  display: block;
}
#navbar .user-menu .user img {
  background: #fff;
  height: 100%;
  border-radius: 50px;
}
#navbar a {
  color: #fff;
}
#navbar .logout {
  cursor: pointer;
}


